body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.save-btn{
    float: right;
}
.logo-position{
    align-self: center;
}

.logo{
    display: flex;
    justify-content: center;
    flex-direction: column;
}
.global{
    margin: 0% 5% 5% 5%;
}
.delete-btn{
    float: right;
}
.book-list-wrapper{
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
}

.book-column{
    border: solid;
    border-width: 2px;
    margin: 0.5% 0 0.5% 0;
}

.book-image{
    margin: 10px;

}

.book-inner-flex{
    display: flex;
    flex-direction: row;
    wrap: nowrap;
}
.description{
    margin-left: 10px;
}
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

